<template>
  <!-- 我的专题详情 -->
  <div class="myspecialDetail">
    <div class="myspecialDetail1">
      <p class="ellipsis-aRow">{{ formdata.subjectName }}</p>
      <div class="myspecialDetail1_fix">共{{ total }}个资源</div>
      <div class="index_con_box1lbcon_tag" v-if="this.formdata.keyword">
        <span
          v-for="(item, index) in formdata.keyword"
          :key="index"
          class="el-tag el-tag--info el-tag--light"
          >{{ item }}</span
        >
      </div>
      <span
        >来源：{{
          formdata.sourceName === "distribute"
            ? formdata.sourceDeptName
            : callDictionaries(acquireSourcelist, formdata.sourceName)
        }}</span
      >
    </div>
    <div class="myspecialDetail2">
      <div class="mydetail_title">
        <p>商品列表</p>
      </div>
      <div class="myspecialDetail2_box">
        <li v-for="(item, index) in tabledata" :key="index">
          <div @click="toDetail(item)">
            <el-image class="myspecialDetail2_img" :src="item.cover"></el-image>
          </div>
          <div class="myResources2_b">
            <span v-if="item.sourceFile">{{ item.sourceFile }}</span>
            <p
              @click="toDetail(item)"
              class="ellipsis-aRow"
              :title="item.goodsTitle"
            >
              {{ item.goodsTitle }}
            </p>
          </div>
        </li>
      </div>
      <noData v-show="!tabledata.length"></noData>
      <div class="myResources3">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryParams.pageNum"
          :page-sizes="[12, 24, 36, 48, 60]"
          :page-size="queryParams.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import {
  selectSubjectBase,
  selectSubjectGoodsList,
} from "@/api/special/special";
import {
  teachingAttrList, //字典值
} from "@/api/bookstore/bookstore";
import noData from "@/components/noData.vue";
export default {
  data() {
    return {
      //查询参数
      id: undefined,
      queryParams: {
        pageNum: 1,
        pageSize: 12,
      },
      formdata: {},
      tabledata: [],
      total: 0,
      acquireSourcelist: [], //来源字典
    };
  },
  components: { noData },
  created() {
    this.id = this.$route.query.subjectId;
    this.getselectSubjectBase();
    this.getresource_source();
  },
  watch: {
    //监听路由变化
    $route() {
      if (this.$route.path === "/special/myspecialDetail") {
        if (this.$route.query.subjectId) {
          this.id = this.$route.query.subjectId;
          this.getselectSubjectBase();
        }
      }
    },
  },
  methods: {
    getselectSubjectBase() {
      selectSubjectBase({ id: this.id }).then((res) => {
        if (res.code == 200) {
          this.formdata = res.data;
          if (this.formdata.keyword) {
            this.formdata.keyword = this.formdata.keyword.split(",");
          }
          var newqueryParams = JSON.parse(JSON.stringify(this.queryParams));
          newqueryParams.id = res.data.id;
          selectSubjectGoodsList(newqueryParams).then((res2) => {
            if (res.code == 200) {
              this.tabledata = res2.rows;
              this.total = res2.total;
            }
          });
        }
      });
    },
    getresource_source() {
      //来源的字典
      teachingAttrList("resource_source").then((res) => {
        this.acquireSourcelist = res.data;
      });
    },
    toDetail(item) {
      if (item.goodsType == "paper") {
        let url = this.$router.resolve({
          path: "/bookstore/answer",
          query: {
            goodsId: item.goodsId,
            resourceType: this.$route.query.resourceType,
            resource: this.$route.query.resource,
            relevanceId: this.$route.query.relevanceId,
            typeName: "0", //判断是否能加入书架
          },
        });
        window.open(url.href, "_blank");
      } else if (item.goodsType == "train") {
        let url = this.$router.resolve({
          path: "/willingLearn/courseQuestions",
          query: {
            goodsId: item.goodsId,
          },
        });
        window.open(url.href, "_blank");
      } else {
        this.$router.push({
          path: "/onlineReading",
          query: {
            goodsId: item.goodsId,
            resourceType: this.$route.query.resourceType,
            resource: this.$route.query.resource,
            relevanceId: this.$route.query.relevanceId,
            typeName: "0", //判断是否能加入书架
          },
        });
      }
    },
    //每页条数
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getselectSubjectBase();
    },
    //当前页面
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getselectSubjectBase();
    },
  },
};
</script>

<style scoped>
.myspecialDetail {
  width: 1400px;
  margin: 0 auto;
  overflow: hidden;
}
.myspecialDetail1 {
  width: 100%;
  float: left;
  background: #fffefd;
  padding: 40px;
  box-sizing: border-box;
}
.myspecialDetail1 p {
  font-size: 16px;
  margin-bottom: 18px;
  width: calc(100% - 200px);
  float: left;
  font-family: PingFang SC;
  font-weight: bold;
  color: #333333;
}
.myspecialDetail1 > span {
  font-size: 14px;
  width: 100%;
  float: left;
  font-family: PingFang SC;
  font-weight: 400;
  color: #999999;
}
.myspecialDetail1_fix {
  font-size: 14px;
  padding: 7px 11px;
  float: right;
  font-family: PingFang SC;
  font-weight: 400;
  color: #0081ce;
  line-height: 9px;
  background: #ffffff;
  border: 1px solid #0081ce;
  border-radius: 6px;
}
.myspecialDetail2 {
  width: 100%;
  float: left;
  background: #fffefd;
  min-height: 100px;
  margin-top: 16px;
  margin-bottom: 40px;
}
.mydetail_title {
  width: 100%;
  float: left;
  border-bottom: 1px solid #f2f2f2;
  height: 52px;
  line-height: 52px;
}
.mydetail_title p {
  font-size: 16px;
  line-height: 52px;
  margin-left: 30px;
  border-bottom: 2px solid #2e9adb;
  float: left;
  padding: 0 8px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #2e9adb;
}
.myspecialDetail2_box {
  width: 100%;
  float: left;
  padding: 30px 40px;
  box-sizing: border-box;
}
.myspecialDetail2_box li {
  width: 170px;
  height: 214px;
  float: left;
  list-style: none;
  margin-right: 60px;
  margin-bottom: 30px;
}
.myspecialDetail2_box li:nth-child(6n) {
  margin-right: 0;
}
.myResources2_b {
  width: 100%;
  float: left;
}
.myResources2_b span {
  float: left;
  margin-right: 7px;
  padding: 0 10px;
  height: 24px;
  background: #f9fdff;
  border: 1px solid #0081ce;
  border-radius: 6px;
  font-size: 13px;
  line-height: 24px;
  text-align: center;
  font-family: PingFang SC;
  font-weight: 400;
  color: #0081ce;
}
.myResources2_b p {
  cursor: pointer;
  font-size: 14px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #333333;
  line-height: 24px;
}
.myspecialDetail2_img {
  width: 170px;
  height: 170px;
  border-radius: 5px;
  margin-bottom: 18px;
  box-shadow: 0 0 3px #ccc;
  cursor: pointer;
}
.index_con_box1lbcon_tag {
  width: 100%;
  float: left;
  margin-bottom: 23px;
}
.index_con_box1lbcon_tag .el-tag {
  height: 28px;
  line-height: 28px;
  float: left;
  margin-right: 16px;
  font-size: 14px;
  font-family: PingFang SC;
  font-weight: 400;
  color: #333333;
  background: #f2f2f2;
  border-radius: 6px;
  margin-bottom: 8px;
}
.myResources3 {
  float: right;
  margin: 20px;
}
</style>
